/* absolute vertical align center */
/* start css reset */
html,
body,
div,
span,
h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
.small,
strike,
sub,
sup,
tt,
var,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
br,
figure,
figcaption,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
strong,
time,
mark,
.mark,
audio,
video,
button,
input {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

:after, :before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  -webkit-touch-callout: none;
  -ms-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -webkit-tap-highlight-color: rgba(0, 165, 181, 0.1);
}

html, body {
  height: 100%;
}

/*
* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}
*/
a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

a:link,
a:visited {
  text-decoration: none;
}

a:hover,
a:active {
  text-decoration: none;
}

address,
em,
i {
  font-style: normal;
}

img {
  border: none;
  vertical-align: middle;
}

ol, ul, li {
  list-style: none;
}

li, p, dt, dd, div, span {
  white-space: normal;
}

table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset {
  border: none;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  font-weight: normal;
}

legend,
caption {
  width: 1px;
  height: 1px;
  overflow: hidden;
  visibility: hidden;
  line-height: 0;
  font-size: 0;
  position: absolute;
  left: -999px;
}

label {
  cursor: pointer;
  font-weight: normal;
}

input, select, option, button, textarea {
  outline: none;
  appearance: none;
}

textarea {
  resize: none;
}

button,
input[type=button] {
  padding: 0;
  margin: 0;
  border: none;
  vertical-align: middle;
  cursor: pointer;
}

input {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: middle;
}

input[type=text]::-ms-clear {
  display: none;
}

input[type=password]::-ms-clear {
  display: none;
}

button:focus {
  outline: 0;
}

button {
  background: none;
}

.clearfix {
  *zoom: 1;
}

.clearfix:after, .footer-inner:after, .tabs-line:after, .contents-group:after, .add-curr-item:after {
  content: "";
  display: block;
  clear: both;
}

