html,
body {
  font-family: "NotoSansKR", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #525252;
  line-height: 1.4;
  background-color: #f6f7fa;
}

.ft-bold {
  font-weight: bold;
}

.ft-500 {
  font-weight: 500;
}

.ct {
  text-align: center !important;
}

.lt {
  text-align: left !important;
}

.rt {
  text-align: right !important;
}

.bd-none {
  border: none !important;
}

.pd-none {
  padding: 0 !important;
}

.float-lt {
  float: left !important;
}

.float-rt {
  float: right !important;
}

.wd-half {
  width: 50%;
}

.txt-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
}

.word-bk {
  word-wrap: break-word;
  word-break: keep-all;
}

.required:after {
  content: "";
  display: inline-block;
  width: 4px;
  height: 4px;
  margin-left: 6px;
  border-radius: 50%;
  vertical-align: middle;
  background-color: #ff4d4d;
}

.after-line {
  position: relative;
}
.after-line:after {
  content: "";
  position: absolute;
  display: block;
  width: 1px;
  height: 16px;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  background-color: #848499;
}

/* router page change motion */
.page-change-enter-active {
  transition: all 0.35s ease;
}

.page-change-leave-active {
  transition: all 0.75s ease;
}

.page-change {
  position: absolute;
}

.page-change-enter {
  opacity: 0;
  transform: scale(0.8);
}

.page-change-leave-to {
  opacity: 0;
  transform: translateY(100%) rotateZ(45deg);
}

/* slide out motion */
.slide-out-enter-active {
  transition: all 0.75s ease;
}

.slide-out-leave-active {
  transition: all 0.75s;
}

.slide-out-enter {
  opacity: 0;
  transform: translateX(10%);
}

.slide-out-leave-to {
  opacity: 0;
  transform: translateX(-10%);
}

/* slide out motion */
.slideY-enter-active {
  transition: all 0.75s ease;
}

.slideY-leave-active {
  transition: all 0.75s;
}

.slideY-enter {
  opacity: 0;
  transform: translateY(20%);
  transition: all 0.75s ease;
}

.slideY-leave-to {
  opacity: 0;
  transform: translateY(-20%);
}

/* slide out motion */
.fade-enter-active {
  transition: all 0.75s ease;
}

.fade-leave-active {
  transition: all 0.75s;
}

.fade-enter {
  opacity: 1;
}

.fade-leave-to {
  opacity: 0;
}

.shake {
  animation: shake-horizontal 0.8s cubic-bezier(0.455, 0.03, 0.515, 0.955) both;
}

.short-shake {
  animation: shake-horizontal 0.3s cubic-bezier(0.455, 0.03, 0.515, 0.955) both;
}

/**
 * ----------------------------------------
 * animation shake-horizontal
 * ----------------------------------------
 */
@keyframes shake-horizontal {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70% {
    transform: translateX(-10px);
  }
  20%, 40%, 60% {
    transform: translateX(10px);
  }
  80% {
    transform: translateX(8px);
  }
  90% {
    transform: translateX(-8px);
  }
}
.btn.is-ripple {
  position: relative;
  overflow: hidden;
  border-radius: 0.25rem;
  cursor: pointer;
}

.ripple-effect {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  background: radial-gradient(rgba(255, 255, 255, 0.2) 0, rgba(255, 255, 255, 0.3) 40%, rgba(255, 255, 255, 0.4) 50%, rgba(255, 255, 255, 0.5) 60%, rgba(255, 255, 255, 0) 70%);
  animation: ripple 0.6s linear;
  transition-property: transform, opacity;
  transform: scale(0);
  pointer-events: none;
}

@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}
.modal-enter-active, .modal-leave-active {
  transition: all 0.6s cubic-bezier(0.68, -0.6, 0.32, 1.6);
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-leave-active .popup-dialog, .modal-leave-active .modal-dialog {
  transform: translate(-50%, -50%) scale(0.3);
  transition: all 0.6s cubic-bezier(0.68, -0.6, 0.32, 1.6);
}

[class*=dropdown-] .icon-chevron-down {
  transform-origin: 50% 50%;
  transition: all 0.3s ease-in-out;
  transform: rotate(0deg);
}
[class*=dropdown-].active .icon-chevron-down {
  transform: rotate(-180deg);
}

.slideUp-enter-active, .slideUp-leave-active {
  transition: all 0.6s cubic-bezier(0.68, -0.6, 0.32, 1.6);
}

.slideUp-enter {
  opacity: 0;
  transform: translateY(-10%);
}

.slideUp-leave-active {
  opacity: 0;
  transform: translateY(-10%);
}

.trans-list {
  display: inline-block;
  margin-right: 10px;
}

.trans-list-enter-active, .trans-list-leave-active {
  transition: all 0.6s cubic-bezier(0.68, -0.6, 0.32, 1.6);
}

.trans-list-enter {
  opacity: 0;
  transform: scale(0.3);
}

.trans-list-leave-active {
  opacity: 0;
  transform: scale(0.3);
}

