.tb-cnt .tb.list tbody tr td .txt-underline {
  font-weight: 500;
}

.card-body.list-type, .card-body.form-type {
  padding: 0 !important;
}

/* border-none */
.tb-cnt table.tb tr:not(:last-child).bdr-none {
  border-bottom: none;
}
.tb-cnt table.tb tr:not(:last-child).bdr-none th {
  border-bottom: none;
}

.tb-nodata {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%);
  transform: translateX(-50%);
  line-height: 1.5;
  text-align: center;
  color: #6e6b7b;
}

.ico-toggle {
  position: relative;
  padding-right: 23px;
}
.ico-toggle:after {
  font-family: "icomoon" !important;
  content: "\e842";
  display: inline-block;
  position: absolute;
  top: 0;
  right: 5px;
  font-size: 14px;
  border-top: 0;
  transform-origin: 50% 50%;
  transform: rotate(0deg);
  transition: all 0.3s ease-in-out;
}
.ico-toggle.type2:after {
  top: -5px;
  right: 0;
  font-size: 19px;
  color: #5e5873;
}

.ico-toggle.active:after {
  transform: rotate(-180deg);
}

/*  form */
.tb-cnt .tb.form tbody tr.request-form th {
  background-color: #fff4d4;
}
.tb-cnt .tb.form tbody tr.request-form td {
  background-color: #fffaeb;
}
.tb-cnt .tb.form tbody tr.request-form td > textarea {
  height: 134px;
}

.border-radius {
  border-top: 5px solid #f3f2f7;
  border-bottom: 5px solid #fff;
}
.border-radius th {
  padding: 5px 16px 10px !important;
}
.border-radius tr:last-child td {
  padding: 14px 16px 9px !important;
}

.table-cnt-top {
  border-radius: 8px;
  box-shadow: inset 0 -1px 0 0 #e9e6f0;
  background-color: #fff;
  overflow: hidden;
}

/*    */
.tb-cnt .tb.chk-list thead tr th {
  text-align: left;
  font-weight: 500;
}
.tb-cnt .tb.chk-list thead tr th:first-child {
  padding-left: 40px;
}
.tb-cnt .tb.chk-list thead tr th:first-child label {
  font-weight: 500;
}
.tb-cnt .tb.chk-list tbody tr td {
  text-align: left;
}
.tb-cnt .tb.chk-list tbody tr td:first-child {
  padding-left: 40px;
}
.tb-cnt .tb.chk-list tbody tr td:first-child label {
  font-size: 14px;
  font-weight: 500;
}
.tb-cnt .tb.chk-list .chk-list-wrap {
  display: grid;
