header {
  width: 100%;
  z-index: 100;
}

nav {
  position: relative;
}

.header-container {
  position: fixed;
  top: 0;
  width: 100%;
  height: 58px;
  background-color: #64646e;
  text-align: center;
  background-repeat: repeat-x;
  border-bottom: 4px solid;
  z-index: 10;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.35);
  left: 0;
  right: 0;
  border-color: #eff2d3;
}

/* ================= header custom ================= */
/* start: header-navbar */
.header-navbar {
  width: calc(100vw - 328px);
  min-width: 1352px !important;
  max-width: 1592px !important;
  font-family: "NotoSansKR", sans-serif !important;
  position: absolute;
  top: 30px;
  left: 304px;
  border-radius: 8px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.14);
  background-color: #fff;
}

.header-navbar .navbar-container {
  padding: 0;
}

.bookmark-wrapper {
  margin-top: 4px;
}

.bookmark-icons .nav-item {
  text-align: center;
  font-size: 12px;
  margin-right: 30px;
}
.bookmark-icons .nav-item:first-child {
  margin-left: 21px;
}
.bookmark-icons .nav-item .nav-link {
  position: relative;
}
.bookmark-icons .nav-item .nav-link .tit {
  display: block;
}
.bookmark-icons .nav-item .nav-link .badge {
  z-index: 1;
  position: absolute;
  top: -6px;
  left: 50%;
  font-size: 10px !important;
  padding: 0.23rem 0.3rem;
  min-width: auto;
  height: 17px;
}
.bookmark-icons .nav-item .nav-link .badge.bg-info {
  width: 36px;
  border-radius: 45px;
}
.bookmark-icons .nav-item .nav-link .badge.bg-danger {
  width: 17px;
  border-radius: 50%;
}
.bookmark-icons .nav-item .nav-link i {
  font-size: 24px;
  vertical-align: middle;
  color: #4a4646;
  font-weight: 300;
}

a.dropdown-user-link {
  display: flex;
  flex-direction: row;
  padding-right: 20px !important;
}
a.dropdown-user-link .user-name {
  display: block;
  margin-bottom: 0 !important;
}
a.dropdown-user-link .icon-chevron-down {
  align-self: center;
  font-size: 16px;
}
a.dropdown-user-link .user-status {
  display: block;
  margin-top: 3px;
  text-align: right;
  font-size: 11px;
  font-weight: 300;
}

.user-menu {
  position: absolute;
  top: 48px;
  right: 0;
  padding: 10px 0;
  width: 200px;
  border-radius: 6px;
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.14);
  background: #fff;
}
.user-menu li a {
  display: block;
  padding: 10px 0 10px 22px;
}
.user-menu li i {
  font-size: 20px;
  vertical-align: text-top;
}
.user-menu li .item {
  display: inline-block;
  margin-left: 10px;
  font-size: 14px;
  font-weight: bold;
  color: #6e6b7b;
}

/* end: header-navbar */
/*sub page container*/
/* start: aside-menu */
aside {
  z-index: 210;
  position: fixed;
  top: 60px;
  left: 0;
  width: 198px;
  height: 100vh;
}
aside + .sub-container {
  margin: 80px 0 0 198px;
}

.aside-header {
  width: 280px;
  position: relative;
  padding: 2.35rem 1rem 3.3rem 1.64rem;
  transition: 300ms ease all, background 0s;
  background: #262731;
}

.aside-content .navigation {
  padding: 10px 0 20px 0;
  margin: 0 22px;
}
.aside-content .navigation li a i {
  height: 20px;
  width: 20px;
  font-size: 20px;
  text-align: center;
  margin-right: 14px;
  opacity: 0.4;
  vertical-align: middle;
}
.aside-content .aside-tit {
  color: #ffffff;
  font-size: 13px;
  vertical-align: bottom;
}

.aside-menu.menu-dark .navigation li a {
  width: 100%;
  color: #d0d0d0;
  padding: 6px 13px;
  line-height: 1.45;
}

.nav-item-tit {
  padding: 20px 0 5px 0;
  margin: 0 14px;
  color: #72747f;
  font-size: 11px;
}

.nav-item ul > li > a {
  position: relative;
  transition: all 0.5s ease;
}
.nav-item ul > li > a:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 34px;
  border-radius: 4px;
  background: #7e72f2;
  transform-origin: 0 50%;
  opacity: 0;
  transform: scaleX(0);
  transition: all 0.5s ease;
  z-index: -1;
}
.nav-item ul > li.active > a {
  margin-left: 3px;
}
.nav-item ul > li.active > a:after {
  opacity: 1;
  transform: scaleX(1);
}
.nav-item ul > li.active > a > i {
  color: #ffffff;
  opacity: 1;
}

/*  main-tit */
.more-btn > i {
  color: #ffffff;
  opacity: 1 !important;
}

/*  */
.logo .img-cnt {
  display: inline-block;
  width: 40px;
  height: 30px;
}
.logo .img-cnt > img {
  width: 100%;
}
.logo .logo-tit {
  display: inline-block;
  font-size: 18px;
  font-weight: 500;
  color: #ffffff;
  vertical-align: top;
  padding-top: 5px;
}
.logo > strong {
  vertical-align: middle;
  padding-left: 7px;
}

/* end: aside-menu */
/* ===================== tooltip custom ===================== */
.tooltip {
  position: relative !important;
  display: inline-block;
  opacity: 1;
}

.tooltip .tooltip-text {
  display: block;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  transform: translateX(-50%);
}
.tooltip .tooltip-text.top {
  top: -15px;
  left: 50%;
}
.tooltip .tooltip-text.top:after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 44%;
  border-top: 6px solid black;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
}
.tooltip .tooltip-text.btm {
  top: 68px;
  left: 50%;
}
.tooltip .tooltip-text.btm:after {
  content: "";
  position: absolute;
  top: -5px;
  left: 44%;
  border-bottom: 6px solid black;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
}
.tooltip .tooltip-text.right {
  top: 27px;
  right: -127%;
}
.tooltip .tooltip-text.right:after {
  content: "";
  position: absolute;
  top: 7px;
  left: -7px;
  border-right: 7px solid black;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
}
.tooltip .tooltip-text.left {
  top: 27px;
  left: -51%;
}
.tooltip .tooltip-text.left:after {
  content: "";
  position: absolute;
  top: 7px;
  right: -7px;
  border-left: 7px solid black;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
}

.dropdown-toggle.btn-outline-primary::after {
  background-image: url("../images/common/dropdown-arrow-primary.svg");
}
.dropdown-toggle.btn-outline-secondary::after {
  background-image: url("../images/common/dropdown-arrow-secondary.svg");
}
.dropdown-toggle.btn-outline-success::after {
  background-image: url("../images/common/dropdown-arrow-success.svg");
}
.dropdown-toggle.btn-outline-danger::after {
  background-image: url("../images/common/dropdown-arrow-danger.svg");
}
.dropdown-toggle.btn-outline-warning::after {
  background-image: url("../images/common/dropdown-arrow-warning.svg");
}
.dropdown-toggle.btn-outline-info::after {
  background-image: url("../images/common/dropdown-arrow-info.svg");
}

.wrapper {
  position: relative;
  overflow-x: hidden;
}
.wrapper #contents {
  overflow-x: hidden;
  width: 100%;
}

.sub-container {
  min-width: 910px;
  padding: 16px 30px 200px 30px;
  background-color: #f8f9fb;
  position: relative;
}
.sub-container .inner {
  position: relative;
  max-width: 1196px;
  margin: 0 auto;
}
.sub-container .sub-description {
  color: #3a3a3a;
  font-size: 14px;
}

.container {
  position: relative;
  max-width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

.content-header {
  overflow: hidden;
}

.contents-wrap {
  margin-left: 280px;
}

.contents {
  width: 100%;
  min-width: 1400px;
  max-width: 1640px;
  padding: 112px 24px 20px;
}

.card.tb-layout {
  width: calc(100vw - 328px);
  min-width: 1352px;
  max-width: 1592px;
  transition-timing-function: ease;
}

.aside-menu {
  z-index: 1031;
  position: absolute;
  display: table-cell;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  background: #262731;
  transition: 0.4s cubic-bezier(0.25, 0.8, 0.25, 1), background 0s;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
}
.aside-menu.menu-fixed {
  position: fixed;
}

.sub-content {
  margin-left: 15px;
}

.semi-dark-layout .aside-content {
  overflow-y: scroll;
  height: calc(100vh - 150px);
  color: #b4b7bd;
}

/* ================= card custom ================= */
.card {
  border-radius: 8px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.14);
}

.radius-none {
  border-radius: 0;
}

.card-title {
  font-size: 16px !important;
  font-weight: bold !important;
  color: #3a3d44;
}

.col-6.bd-right {
  border-right: 2px solid #f0f0f0;
}

/* slide-container */
.slide-cnt {
  position: relative;
}
.slide-cnt .slide-view {
  position: relative;
  overflow: hidden;
  padding: 20px 0;
}
.slide-cnt .slide-view:after {
  width: 100px;
  height: 100%;
  content: "";
  position: absolute;
  display: block;
  -webkit-filter: blur(10px);
  filter: blur(10px);
  top: 3px;
  right: -28px;
  z-index: 1;
  background-color: #f6f7fa;
}
.slide-cnt .prev-btn, .slide-cnt .next-btn {
  z-index: 1000;
  position: absolute;
  top: 50%;
}
.slide-cnt .prev-btn > i, .slide-cnt .next-btn > i {
  font-size: 24px;
}
.slide-cnt .prev-btn {
  left: 0;
}
.slide-cnt .next-btn {
  right: 0;
}

.slide-wrap {
  position: relative;
  width: 300%;
}
.slide-wrap .item {
  float: left;
  margin-right: 10px;
  /* slide gift hover */
}
.slide-wrap .item:hover .card-body.slide {
  border: 1px solid #7367f0;
  border-radius: 8px;
  padding: 14px;
  color: #525252;
}

.slide-contents {
  position: relative;
}
.slide-contents .img-cnt {
  width: 240px;
  float: left;
}
.slide-contents .img-cnt > img {
  width: 100%;
}
.slide-contents .txt-cnt {
  position: relative;
  width: 260px;
  float: left;
  margin-left: 15px;
}
.slide-contents .txt-cnt .tit-txt {
  font-size: 14px;
  font-weight: bold;
  color: #6e6b7b;
}
.slide-contents .txt-cnt .detail {
  line-height: 22px;
  padding: 5px 0 12px 0;
  font-size: 12px;
}
.slide-contents .txt-cnt .num-cnt {
  position: absolute;
  bottom: 0;
  right: -3px;
  font-size: 12px;
}

.card-body.slide {
  padding: 15px;
}

.login {
  position: relative;
  width: 100%;
  height: 100%;
}
.login .container {
  position: relative;
  width: calc(100% - 660px);
  height: 100vh;
}
.login .container::after {
  content: "";
  display: block;
